@import "./_global.scss";
@import "./_mixin.scss";

#root {
  color: #65666b;
  background-color: #fff;
  font-size: 24px;
  line-height: 1;
  @include bgFill("../images/bg-body.png");
  background-position: center -110px;
}
.app {
  padding-bottom: 220px;
}

.rent-header {
  display: flex;
  padding: 45px 30px 28px;
}

.rent-logo {
  width: 100px;
  height: 100px;
  background-color: #333;
  .rent-logo-pic {
    width: 100%;
    height: 100%;
  }
}

.rent-header-content {
  flex: 1;
  padding: 3px 24px;
  color: #fff;
  .rent-title {
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  .rent-tag {
    display: inline-block;
    margin-right: 16px;
    padding: 8px 14px;
    background-color: rgba(255, 255, 255, 0.14);
    font-size: 22px;
  }
}

.rent-info-card {
  width: 722px;
  height: 392px;
  margin: 0 auto 28px;
  @include bgFill("../images/bg-rent-info-card.png");
  .rent-info-title {
    color: #a8a9ab;
    line-height: 34px;
  }
}
.user-handle {
  padding-top: 54px;
  padding-bottom: 22px;
  border-bottom: 2px solid rgba(216, 216, 216, 0.2);
  display: flex;
  .user-handle-item {
    position: relative;
    padding-left: 58px;
    width: 50%;
    &::before {
      content: "";
      position: absolute;
      bottom: 33px;
      width: 32px;
      height: 32px;
    }
    &.amount {
      &::before {
        right: 30px;
        @include bgFill("../images/icon-edit.svg");
      }
    }
    &.term {
      &::before {
        right: 160px;
        @include bgFill("../images/icon-select.svg");
      }
    }
    .user-handle-input {
      position: relative;
      z-index: 1;
      width: 100%;
      line-height: 96px;
      font-size: 68px;
      font-weight: bold;
      color: #fe4949;
      font-family: Arial;
      &.amount-input {
        padding-right: 80px;
      }
    }
  }
}
.calc-container {
  display: flex;
  .calc-item {
    flex: 1;
    text-align: center;
    padding-top: 36px;
    padding-bottom: 62px;
    .calc-amount {
      margin-bottom: 2px;
      line-height: 50px;
      font-size: 36px;
      font-weight: bold;
      font-family: Arial;
      .calc-amount-unit {
        font-size: 26px;
      }
    }
  }
}

.product-info-list {
  padding: 0 30px;
}

.product-info-item {
  overflow: hidden;
  & + .product-info-item {
    margin-top: 48px;
  }
  .product-info-title {
    margin-bottom: 16px;
    font-size: 36px;
    line-height: 50px;
    color: #23262c;
  }
  .product-info-content {
    font-size: 28px;
    line-height: 40px;
    &.condition-list {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: -16px;
      margin-right: -70px;
      .condition-item {
        position: relative;
        margin-right: 70px;
        margin-bottom: 16px;
        padding-left: 40px;
        &::before {
          content: "";
          position: absolute;
          top: 3px;
          left: 0;
          width: 30px;
          height: 30px;
          @include bgFill("../images/icon-condition.svg");
        }
      }
    }
  }
}

.rent-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transform: translateZ(1);
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 4px 8px 0px rgba(8, 12, 25, 0.6);
  padding: 26px 85px;
  .rent-agreement {
    text-align: center;
    font-size: 24px;
    line-height: 34px;
    margin-bottom: 28px;
    .rent-agreement-link {
      color: #fe4949;
    }
    .rent-agreement-icon {
      display: inline-block;
      vertical-align: top;
      margin-right: 6px;
      width: 34px;
      height: 34px;
      @include bgFill("../images/icon-notCheck.svg");
      &.checked {
        @include bgFill("../images/icon-checked.svg");
      }
    }
  }
  .apply-btn {
    width: 580px;
    padding: 14px 0;
    @include bgFill("../images/bg-apply.png");
    font-size: 40px;
    line-height: 56px;
    color: #fff;
    font-weight: bold;
  }
}

.mobileSelect .content .btnBar {
  border-bottom: 0;
  height: 50px;
}

.product-model-mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5000;
  background-color: rgba(0, 0, 0, 0.5);
}
.product-model-container {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 5001;
  transform: translate(-50%, -50%);
  width: 630px;
}
.product-model-close {
  position: absolute;
  top: -60px;
  right: 0;
  width: 60px;
  height: 60px;
  @include bgFill("../images/icon-close.png");
}
.product-model-header {
  height: 240px;
  @include bgFill("../images/bg-model-header.png");
}
.product-model-content {
  height: 570px;
  background-color: #fff;
  border-radius: 0px 0px 12px 12px;
  padding: 41px 30px;
}
.product-item {
  display: flex;
  align-items: center;
  color:#65666B;
  .product-item-info {
    display: flex;
    align-items: center;
    width: 412px;
    height: 160px;
    padding-left: 40px;
    @include bgFill('../images/bg-product-info.png');
    .product-logo-pic {
      width: 84px;
      height: 84px;
    }
    .product-intro {
      flex: 1;
      padding-left: 18px;
      text-align: left;
      .product-title {
        font-size: 34px;
        line-height: 48px;
      }
      .product-quota {
        font-size: 26px;
        line-height: 36px;
        .product-quota-text {
          color: #FE4949;
        }
      }
    }
  }
  .product-item-apply {
    width: 158px;
    height: 144px;
    @include bgFill('../images/bg-product-apply.png');
  }
}
#root .modal {
  width: auto;
  max-width: 500px;
}
#root .modal-content-wrap {
  padding: 30px;
}
#root .modal-title {
  font-size: 30px;
  margin-bottom: 15px;
}
#root .modal-inner {
  font-size: 28px;
}
#root .modal-button {
  height: 80px;
  line-height: 80px;
  border-top: 1px solid #dcdcdc;
  font-size: 28px;
}
#root .notice {
  font-size: 26px;
  padding: 30px !important;
}

#root .indicator {
  padding: 30px 40px;
}

#root .indicator .spinner {
  width: 60px;
  height: 60px;
}